import React from 'react';
import classes from './ProfileInfo.module.css';
import Preloader from '../../Common/Preloader/Preloader';
import yes from '../../../assets/images/yes.png';
import no from '../../../assets/images/no.png';
import facebook from '../../../assets/images/facebook.png';
import web from '../../../assets/images/web.png';
import vk from '../../../assets/images/vk.png';
import twitter from '../../../assets/images/twitter.png';
import instagram from '../../../assets/images/instagram.png';
import youtube from '../../../assets/images/youtube.png';
import github from '../../../assets/images/github.png';
import mainLink from '../../../assets/images/mainLink.png';
import ProfileStatusWithHooks from './ProfileStatusWithHooks';

const ProfileInfo = (props) => {
  if (!props.profile) {
    return <Preloader />
  }

  return (
    <div className={classes.content}>
      {/* <img src='https://www.w3schools.com/howto/img_snow_wide.jpg' /> */}
      <div className={classes.profileContent}>
        <div>
          <img src={props.profile.photos.large} />
          <ProfileStatusWithHooks status={props.status} updateProfileStatus={props.updateProfileStatus}/>
        </div>
        <div className={classes.descriptionBlock}>
          <div className={classes.fullName}>
            {props.profile.fullName}
          </div>
          <div className={classes.aboutMy}>
            About me: {props.profile.aboutMe}
          </div>
          <div className={classes.job}>
            Am I looking for a job?
          {props.profile.lookingForAJob ? <img src={yes} /> : <img src={no} />}
          </div>
          <div className={classes.job}>
            {props.profile.lookingForAJobDescription}
          </div>
        </div>

        <div className={classes.contacts}>
          <div>My contacts:</div>
          <div>
            {props.profile.contacts.facebook
              ? <div>
                <img src={facebook} />
                <a href={props.profile.contacts.facebook} target="_blank"> Facebook </a>
              </div>
              : null}
          </div>
          <div>
            {props.profile.contacts.website
              ? <div>
                <img src={web} />
                <a href={props.profile.contacts.website} target="_blank"> Website </a>
              </div>
              : null}
          </div>
          <div>
            {props.profile.contacts.vk
              ? <div>
                <img src={vk} />
                <a href={props.profile.contacts.vk} target="_blank"> Vk </a>
              </div>
              : null}
          </div>
          <div>
            {props.profile.contacts.twitter
              ? <div>
                <img src={twitter} />
                <a href={props.profile.contacts.twitter} target="_blank"> Twitter </a>
              </div>
              : null}
          </div>
          <div>
            {props.profile.contacts.instagram
              ? <div>
                <img src={instagram} />
                <a href={props.profile.contacts.instagram} target="_blank"> Instagram </a>
              </div>
              : null}
          </div>
          <div>
            {props.profile.contacts.youtube
              ? <div>
                <img src={youtube} />
                <a href={props.profile.contacts.youtube} target="_blank"> YouTube </a>
              </div>
              : null}
          </div>
          <div>
            {props.profile.contacts.github
              ? <div>
                <img src={github} />
                <a href={props.profile.contacts.github} target="_blank"> GitHub </a>
              </div>
              : null}
          </div>
          <div>
            {props.profile.contacts.mainLink
              ? <div>
                <img src={mainLink} />
                <a href={props.profile.contacts.mainLink} target="_blank"> MainLink </a>
              </div>
              : null}
          </div>
        </div>
      </div>
    </div>)
}

export default ProfileInfo;