import React from 'react';
import classes from './Post.module.css';

const Post = (props) => {
  
  return (
    <div className={classes.item}>
      <img src='https://www.dierverzorgerdenise.nl/wp-content/uploads/2015/07/poes1.png' />
      {props.massage}
      <div>
        Like {props.likesCount}
      </div>
    </div>
  )
}

export default Post;