import React from 'react';
import classes from './MyPosts.module.css';
import Post from './Post/Post';
import MyPostReduxForm from './MyPostForm';

const MyPosts = (props) => {

  let postsElements = props.posts.map(post => <Post massage={post.message} likesCount={post.likesCount} key={post.id} />);

  let onAddPost = (values) => {
    props.addPost(values.myPost);
  }

  return (
    <div className={classes.postsBlock}>
      <h3>My posts</h3>
        <MyPostReduxForm onSubmit={onAddPost} />
      <div className={classes.posts}>
        {postsElements}
      </div>
    </div>
  )
}

export default MyPosts;